import { useState } from "react";
import { useContext } from "react";
import { CartContext } from "../context/CartContext";
import styles from "../page.module.css";

const NavBar = ({ categories, tags, setFilteredDishes }) => {
  const [openDropdown, setOpenDropdown] = useState("");
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [selectedTags, setSelectedTags] = useState([]);
  const [sortOrder, setSortOrder] = useState("");

  const handleDropdown = (section) => {
    setOpenDropdown(openDropdown === section ? "" : section);
  };

  const applyFilters = () => {
    let filtered = menu;

    if (selectedCategories.length > 0) {
      filtered = filtered.filter((dish) =>
        selectedCategories.includes(dish.category)
      );
    }

    if (selectedTags.length > 0) {
      filtered = filtered.filter((dish) =>
        selectedTags.some((tag) => dish.tags.includes(tag))
      );
    }

    if (sortOrder === "lowToHigh") {
      filtered = filtered.sort((a, b) => a.price - b.price);
    } else if (sortOrder === "highToLow") {
      filtered = filtered.sort((a, b) => b.price - a.price);
    }

    setFilteredDishes(filtered);
  };

  return (
    <nav className={styles.navBar}>
      <ul>
        <li>
          <button onClick={() => handleDropdown("filtros")}>Filtros</button>
          {openDropdown === "filtros" && (
            <ul className={styles.dropdown}>
              <li>
                <button onClick={() => setSortOrder("lowToHigh")}>
                  Ordenar por Preço: Baixo para Alto
                </button>
              </li>
              <li>
                <button onClick={() => setSortOrder("highToLow")}>
                  Ordenar por Preço: Alto para Baixo
                </button>
              </li>
            </ul>
          )}
        </li>
        <li>
          <button onClick={() => handleDropdown("categorias")}>
            Categorias
          </button>
          {openDropdown === "categorias" && (
            <ul className={styles.dropdown}>
              {categories.map((category) => (
                <li key={category}>
                  <button
                    onClick={() =>
                      setSelectedCategories((prev) =>
                        prev.includes(category)
                          ? prev.filter((cat) => cat !== category)
                          : [...prev, category]
                      )
                    }
                  >
                    {category}
                  </button>
                </li>
              ))}
            </ul>
          )}
        </li>
        <li>
          <button onClick={() => handleDropdown("tags")}>Tags</button>
          {openDropdown === "tags" && (
            <ul className={styles.dropdown}>
              {tags.map((tag) => (
                <li key={tag}>
                  <button
                    onClick={() =>
                      setSelectedTags((prev) =>
                        prev.includes(tag)
                          ? prev.filter((t) => t !== tag)
                          : [...prev, tag]
                      )
                    }
                  >
                    {tag}
                  </button>
                </li>
              ))}
            </ul>
          )}
        </li>
        <li>
          <button onClick={applyFilters}>Aplicar Filtros</button>
        </li>
      </ul>
    </nav>
  );
};

export default NavBar;
